// Navbar.js
import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { getCurrentUser, loginWithGoogle, loginWithEmail, registerWithEmail } from '../auth';
import { useOverlay } from './overlayManager'; // Import the useOverlay hook
import './Navbar.css';
import logo from '../pics/logo192.png';
import Register from '../authpanel/Register';
import SignIn from '../authpanel/SignIn';
import { getFirestore, doc, getDoc } from 'firebase/firestore';

const Navbar = () => {
  const { isRegisterVisible, isLoginVisible, showRegister, hideRegister, showLogin, hideLogin, closeOverlays } = useOverlay();
  const [user, setUser] = React.useState(null);
  const [isAdmin, setIsAdmin] = React.useState(false);
  const navigate = useNavigate();
  const db = getFirestore();

  useEffect(() => {
    const unsubscribe = getCurrentUser(async (user) => {
      setUser(user);
      if (user) {
        const userRef = doc(db, 'users', user.uid);
        const userDoc = await getDoc(userRef);
        if (userDoc.exists()) {
          setIsAdmin(userDoc.data().isAdmin || false);
        }
      }
    });
    return () => unsubscribe();
  }, [db]);

  const handleGoogleLogin = async () => {
    try {
      await loginWithGoogle();
      closeOverlays();
      navigate('/profile');
    } catch (error) {
      console.error("Login failed:", error.message);
      alert(error.message);
    }
  };

  const handleEmailLogin = async (email, password) => {
    try {
      await loginWithEmail(email, password);
      closeOverlays();
      navigate('/profile');
    } catch (error) {
      console.error("Login failed:", error.message);
      alert(error.message);
    }
  };

  const handleEmailRegister = async (email, password) => {
    try {
      await registerWithEmail(email, password);
      closeOverlays();
      navigate('/profile');
    } catch (error) {
      console.error("Registration failed:", error.message);
      alert(error.message);
    }
  };

  return (
    <div className='navbar-container'>
      <div className="navbar">
        <div className="nav-left">
          <img src={logo} className="logo" alt="Anglomova Logo" />
          <div className="nav-title">Anglomova</div>
        </div>
        <div className="nav-panel">
          <NavPanelElement label="Main" icon="home" onClick={() => navigate('/')} />
          <NavPanelElement label="Courses" icon="book" onClick={() => navigate('/courses')} />
          <NavPanelElement label="Campaign" icon="bolt" onClick={() => navigate('/lesson')} />
          <NavPanelElement label="Loot" icon="star" onClick={() => navigate('/materials')} />
          <NavPanelElement label="Pricing" icon="paid" onClick={() => navigate('/pricing')} />
          {isAdmin && (
            <NavPanelElement label="Admin" icon="shield_person" onClick={() => navigate('/admin')} />
          )}
        </div>
        <div className="nav-auth" id="auth-button">
          {user ? (
            <>
              <NavPanelElement label="Profile" icon="account_circle" onClick={() => navigate('/profile')} />
            </>
          ) : (
            <>
              <button className="nav-auth-sign" onClick={showLogin}>Sign in</button>
              <button className="nav-auth-reg" onClick={showRegister}>Register</button>
            </>
          )}
        </div>
      </div>

      {isRegisterVisible && (
        <Overlay title="REGISTER" onClose={hideRegister}>
          <Register onRegister={handleEmailRegister} />
          <p>Already have an account? <span onClick={() => { hideRegister(); showLogin(); }}>SIGN IN</span></p>
        </Overlay>
      )}
      {isLoginVisible && (
        <Overlay title="LOG IN" onClose={hideLogin}>
          <SignIn onLogin={handleEmailLogin} onGoogleLogin={handleGoogleLogin} />
          <p>Don't have an account? <span onClick={() => { hideLogin(); showRegister(); }}>REGISTER</span></p>
        </Overlay>
      )}
    </div>
  );
};

const NavPanelElement = ({ label, icon, onClick }) => (
  <div className="nav-panel-element" onClick={onClick}>
    <span className="material-symbols-outlined">{icon}</span>
    <span>{label}</span>
  </div>
);

const Overlay = ({ title, onClose, children }) => (
  <div className="overlay">
    <div className="overlay-content">
      <h2>{title}</h2>
      <button className="close-btn" onClick={onClose}>X</button>
      {children}
    </div>
  </div>
);

export default Navbar;
