import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { getFirestore, doc, getDoc } from 'firebase/firestore';
import DOMPurify from 'dompurify';
import '../../styles/CurrentLesson.css'; // Ensure you have this CSS file or adjust the path accordingly
import { handleInteraction, PART_KEYS } from './utils';



const LessonPage = () => {
    const { courseID, moduleID, lessonID } = useParams();
    const [lesson, setLesson] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [currentPartIndex, setCurrentPartIndex] = useState(0); // Track the current part
    const db = getFirestore();
  
    useEffect(() => {
        console.log('Attempting to fetch lesson with the following IDs:');
        console.log('Course ID:', courseID);
        console.log('Module ID:', moduleID);
        console.log('Lesson ID:', lessonID);
    
        const fetchLesson = async () => {
            setLoading(true);
            try {
              const lessonRef = doc(db, 'courses', courseID, 'modules', moduleID, 'lessons', lessonID);
              const lessonDoc = await getDoc(lessonRef);
              if (lessonDoc.exists()) {
                setLesson(lessonDoc.data());
              } else {
                setError('Lesson not found');
              }
            } catch (err) {
              setError('Error fetching lesson data. Please try again later.');
            } finally {
              setLoading(false);
            }
          };
      
          fetchLesson();
        }, [courseID, moduleID, lessonID, db]);
      
        useEffect(() => {
          if (lesson) {
            const cleanup = handleInteraction();
            return cleanup;
          }
        }, [lesson, currentPartIndex]);
    
      const handleNext = () => {
        let newIndex = currentPartIndex + 1;
        while (newIndex < 5 && !PART_KEYS.some(key => key.startsWith(`Part${newIndex + 1}/`) && lesson[key])) {
          newIndex++;
        }
        if (newIndex < 5) {
          setCurrentPartIndex(newIndex);
        }
      };
    
      const handlePrevious = () => {
        let newIndex = currentPartIndex - 1;
        while (newIndex >= 0 && !PART_KEYS.some(key => key.startsWith(`Part${newIndex + 1}/`) && lesson[key])) {
          newIndex--;
        }
        if (newIndex >= 0) {
          setCurrentPartIndex(newIndex);
        }
      };
      

      const RenderLessonParts = ({ lesson }) => {
        const currentPartKeys = PART_KEYS.filter(key => key.startsWith(`Part${currentPartIndex + 1}/`));
    
        return (
          <div className="mane">
            {currentPartKeys.map((key, index) => {
              const content = DOMPurify.sanitize(lesson[key] || '');
              if (!content) return null;
    
              if (key.includes('input')) {
                return (
                  <input
                    key={index}
                    type="text"
                    name={key}
                    value={content}
                    onChange={handleInteraction}
                    autocomplete="new-password"
                    autocorrect="off"
                    autocapitalize="none"
                    spellcheck="false"
                  />
                );
              } else if (key.includes('/Type')) {
                return <h1 key={index} dangerouslySetInnerHTML={{ __html: content }} />;
              } else if (key.includes('.Warm-up')) {
                return <div className='warming' key={index} dangerouslySetInnerHTML={{ __html: content }} />;
              } else if (key.includes('.task')) {
                return <h2 key={index} dangerouslySetInnerHTML={{ __html: content }} />;
              } else {
                return <div key={index} className='part' dangerouslySetInnerHTML={{ __html: content }} />;
              }
            })}
          </div>
        );
      };
    
      if (loading) {
        return <div>Loading...</div>;
      }
    
      if (error) {
        return <div>{error}</div>;
      }
    
      return (
        <div>
        <h1>{lesson.CourseID}: {lesson.moduleName}: {lesson.lessonName}</h1>
        <div className='lesson-page'>
          <div className="navigationL" onClick={handlePrevious} disabled={currentPartIndex === 0}>
            <span className="material-symbols-outlined">arrow_back</span>
            <div className="nav-dir">Back</div>
          </div>
          
          <RenderLessonParts lesson={lesson} />
          
          <div className="navigationR" onClick={handleNext} disabled={currentPartIndex === 4}>
            <span className="material-symbols-outlined">arrow_forward</span>
            <div className="nav-dir">Next</div>
          </div>
        </div>
      </div>
      )
    }
    export default LessonPage;