import React, { useState } from 'react';
import axios from 'axios';
import { doc, setDoc } from 'firebase/firestore';
import { db } from './firebase'; // Adjust the import path as needed

const AdminPage = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchAndUploadData = async () => {
    setLoading(true);
    setError(null);

    try {
      // Fetch data from Google Sheets
      const response = await axios.get('https://script.google.com/macros/s/AKfycbw5yemXas7W-NlpXJbACaXx00FsOaUUsPRUW9_OvOo_7o67NVehLkVSB0KK4dctJF5R/exec');
      const structuredData = response.data;

      // Check if the data is being fetched correctly
      console.log('Fetched Data:', structuredData);

      const coursesData = structuredData.courses || {};
      const modulesData = structuredData.modules || {};
      const lessonsData = structuredData.lessons || {};

      // Create ID mappings for courses, modules, and lessons
      const courseIdMap = {};
      const moduleIdMap = {};
      const lessonIdMap = {};

      // Populate mappings for courses
      Object.entries(coursesData).forEach(([index, courseData]) => {
        const courseId = courseData['CourseID'];
        courseIdMap[index] = courseId;
      });

      // Upload courses data
      await Promise.all(Object.entries(coursesData).map(async ([index, courseData]) => {
        const courseId = courseIdMap[index];
        console.log(`Uploading Course: ${courseId}`);
        await setDoc(doc(db, 'courses', courseId), courseData);

        // Create mappings for modules related to this course
        const courseModules = Object.entries(modulesData).filter(([_, moduleData]) => moduleData['CourseID'] === courseId);
        courseModules.forEach(([index, moduleData]) => {
          const moduleId = moduleData['ModuleID'];
          moduleIdMap[index] = moduleId;
        });

        // Upload modules data
        await Promise.all(courseModules.map(async ([index, moduleData]) => {
          const moduleId = moduleIdMap[index];
          console.log(`Uploading Module: ${moduleId} for Course: ${courseId}`);
          await setDoc(doc(db, 'courses', courseId, 'modules', moduleId), moduleData);

          // Create mappings for lessons related to this module
          const moduleLessons = Object.entries(lessonsData).filter(([_, lessonData]) => lessonData['ModuleID'] === moduleId);
          moduleLessons.forEach(([index, lessonData]) => {
            const lessonId = lessonData['LessonID'];
            lessonIdMap[index] = lessonId;
          });

          // Upload lessons data
          await Promise.all(moduleLessons.map(async ([index, lessonData]) => {
            const lessonId = lessonIdMap[index];
            console.log(`Uploading Lesson: ${lessonId} for Module: ${moduleId}`);
            await setDoc(doc(db, 'courses', courseId, 'modules', moduleId, 'lessons', lessonId), lessonData);
          }));
        }));
      }));

      setError(null);
    } catch (error) {
      console.error('Error fetching and uploading data:', error);
      setError('Failed to fetch and upload data.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <h1>Admin Page</h1>
      <button onClick={fetchAndUploadData} disabled={loading}>
        {loading ? 'Uploading...' : 'Fetch and Upload Data'}
      </button>
      {error && <p style={{ color: 'red' }}>{error}</p>}
    </div>
  );
};

export default AdminPage;
