import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { getFirestore, doc, getDoc, collection, query, where, getDocs } from 'firebase/firestore';
import { useAuth } from '../AuthContext';
import '../styles/CurrentLesson.css';
import { useLesson } from '../LessonContext';

const CurrentLesson = () => {
  const { user } = useAuth();
  const { courseContent, setCourseContent, nextLesson, setNextLesson } = useLesson();  // Using useLesson to get and set lesson data
  const [selectedModule, setSelectedModule] = useState(null);
  const [moduleLessons, setModuleLessons] = useState([]);
  const [loading, setLoading] = useState(!courseContent);
  const [error, setError] = useState(null);
  const navigate = useNavigate();
  const db = getFirestore();

  useEffect(() => {
    if (!user || courseContent) return;  // Only fetch if user is available and courseContent hasn't been fetched yet


    const fetchNextLesson = async () => {
      setLoading(true);
      try {
        const userRef = doc(db, 'users', user.uid);
        const userDoc = await getDoc(userRef);
        if (userDoc.exists()) {
          const userData = userDoc.data();
          const courseID = userData.courseID;

          const modulesQuery = query(collection(db, 'courses', courseID, 'modules'));
          const modulesSnapshot = await getDocs(modulesQuery);

          let modules = [];
          let lessons = [];

          for (const moduleDoc of modulesSnapshot.docs) {
            const moduleData = moduleDoc.data();
            modules.push({ id: moduleDoc.id, ...moduleData });

            const lessonsQuery = query(collection(db, 'courses', courseID, 'modules', moduleDoc.id, 'lessons'));
            const lessonsSnapshot = await getDocs(lessonsQuery);
            lessons = lessons.concat(
              lessonsSnapshot.docs.map(doc => ({ id: doc.id, moduleID: moduleDoc.id, courseID, ...doc.data() }))
            );
          }

          const lessonLogQuery = query(collection(db, 'users', user.uid, 'lessonLog'), where('status', '==', 'finished'));
          const lessonLogSnapshot = await getDocs(lessonLogQuery);
          const finishedLessons = lessonLogSnapshot.docs.map(doc => doc.id);

          const remainingLessons = lessons.filter(lesson => !finishedLessons.includes(lesson.id));
          const nextLesson = remainingLessons.length > 0 ? remainingLessons[0] : null;

          setNextLesson(nextLesson);
          setCourseContent({ modules, lessons });
        }
      } catch (error) {
        setError('Error fetching data. Please try again later.');
        console.error('Error fetching next lesson:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchNextLesson();
  }, [user, db, courseContent, setCourseContent, setNextLesson]);

  const handleModuleClick = (module) => {
    setSelectedModule(module);
    // No need to fetch again; just set the selected module

  // Filter the lessons belonging to this module
  if (courseContent) {
    const lessonsForModule = courseContent.lessons.filter(lesson => lesson.moduleID === module.id);
    setModuleLessons(lessonsForModule);
  }
};

  const handleLessonClick = (lesson) => {
      
    if (user.isAdmin || lesson.status === 'finished' || lesson.LessonID === nextLesson?.LessonID) {
      navigate(`/course/${lesson.CourseID}/module/${lesson.ModuleID}/lesson/${lesson.LessonID}`);
    } else {
      alert("You don't have access to this lesson yet.");
    }
  };

  const closeOverlay = () => {
    setSelectedModule(null);
    setModuleLessons([]);
  };

  if (loading || !user) {
    return <div>Loading...</div>; // Show loading until user and data are ready
  }
  
  if (error) {
    return <div>{error}</div>;
  }

  return (
    <div className="lesson-overview">
      <h1>Current Lesson</h1>
      <div className='wrapper'>
        {/* Next Lesson Card */}
        <div className='wrapper-component'>
          {nextLesson && (
            <div
              className="card"
              onClick={() => handleLessonClick(nextLesson)}
              style={{
                backgroundImage: `url(${nextLesson.BGimg})`,
                backgroundSize: 'cover',
                backgroundPosition: 'center'
              }}
            >
              <div className='module-num top'>
                {nextLesson.courseID} <br />
                {nextLesson.moduleName}
              </div>
              <h3>Lesson {nextLesson.lessonNum}:<br />{nextLesson.lessonName}</h3>
            </div>
          )}
        </div>

        {/* Modules List */}
        <div className='wrapper-component right'>
          <h2 className='right-cont'>Content of my Course</h2>
          <div className="modules-list">
            {courseContent.modules.map(module => (
              <div
                key={module.id}
                className="card-item"
                onClick={() => handleModuleClick(module)}
              >
                <img className='listImg' src={module.modImg} alt={module.modName} />
                <div>{module.modName}</div>
              </div>
            ))}
          </div>
        </div>

        {/* Selected Module Details */}
        {selectedModule && (
          <div className="mod">
            <div className="mod-det">
              <div className='mod-det-cont'>
                <div className='mod-det-head'>
                  <div className='mod-det-head-item'>
                    <img className="listImg" src={selectedModule.modImg} alt={selectedModule.modName} />
                    <h3 className='black'>{selectedModule.modName}</h3>
                    <button className="close-button" onClick={closeOverlay}></button>
                  </div>
                  </div>
              {/* Lessons List for Selected Module */}
              <div className="lessons-list">
                  {moduleLessons.map(lesson => (
                    <div
                      key={lesson.LessonID}
                      className="card-item"
                      onClick={() => handleLessonClick(lesson)}
                    >
                      <img className='listImg' src={lesson.BGimg} alt={lesson.lessonName} />
                      <div>{lesson.lessonNum}: {lesson.lessonName}</div>

                      </div>
                  
                  ))}
                </div>

               
                <div
                  className='mod-det-desc long'
                  dangerouslySetInnerHTML={{ __html: selectedModule.modFullDescription }}
                />
               
                <div
                  className='mod-det-item short'
                  dangerouslySetInnerHTML={{ __html: selectedModule.modDescription }}
                />
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default CurrentLesson;
