// OverlayManager.js
import React, { createContext, useState, useContext } from 'react';

// Create a context for managing overlay state
const OverlayContext = createContext();

export const OverlayProvider = ({ children }) => {
  const [isRegisterVisible, setRegisterVisible] = useState(false);
  const [isLoginVisible, setLoginVisible] = useState(false);

  const showRegister = () => setRegisterVisible(true);
  const hideRegister = () => setRegisterVisible(false);

  const showLogin = () => setLoginVisible(true);
  const hideLogin = () => setLoginVisible(false);

  const closeOverlays = () => {
    setRegisterVisible(false);
    setLoginVisible(false);
  };

  return (
    <OverlayContext.Provider value={{ isRegisterVisible, isLoginVisible, showRegister, hideRegister, showLogin, hideLogin, closeOverlays }}>
      {children}
    </OverlayContext.Provider>
  );
};

// Custom hook to use overlay context
export const useOverlay = () => useContext(OverlayContext);
