import React, { useState, useEffect } from 'react';
import DOMPurify from 'dompurify';
import CourseItem from '../CourseItem'; // Adjust the import path as needed
import '../styles/Courses.css';
import { getFirestore, collection, getDocs } from "firebase/firestore";

const Courses = () => {
  const [data, setData] = useState([]);
  const [selectedCourse, setSelectedCourse] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const db = getFirestore();
        const coursesCollection = collection(db, "courses");
        const coursesSnapshot = await getDocs(coursesCollection);
        const coursesData = coursesSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        console.log('Fetched courses:', coursesData); // Debug: Check the structure of fetched data
        setData(coursesData);
      } catch (error) {
        console.error('Error fetching data:', error);
        setError('Failed to load courses. Please try again later.');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const handleCourseClick = (course) => {
    setSelectedCourse(course);
  };

  const handleCloseOverlay = () => {
    setSelectedCourse(null);
  };

  const createMarkup = (htmlContent) => {
    return { __html: DOMPurify.sanitize(htmlContent) };
  };

  return (
    <div>
      <h1>Courses</h1>
      {loading ? (
        <p>Loading...</p>
      ) : error ? (
        <p>{error}</p>
      ) : (
        <div className="courses-grid">
          {data.length > 0 ? (
            data.map(course => (
              <CourseItem key={course.id} course={course} onClick={handleCourseClick} />
            ))
          ) : (
            <p>No courses available.</p>
          )}

          {selectedCourse && (
            <div className="overlay" onClick={handleCloseOverlay}>
              <div className="overlay-grid" onClick={(e) => e.stopPropagation()}>
                <div className="left-grid">
                  <h2>{selectedCourse.courseName}</h2>
                  <img 
                    src={selectedCourse.DetailImage} 
                    alt={selectedCourse.courseName || 'Course image'} 
                    className="detail-image" 
                  />
                </div>
                <div>
                  <p className="detail-heading">
                    <span dangerouslySetInnerHTML={createMarkup(selectedCourse.ShortDescription)} />
                  </p>
                  <p>
                    <span dangerouslySetInnerHTML={createMarkup(selectedCourse.FullDescription)} />
                  </p>
                  <span className="close" onClick={handleCloseOverlay}>
                    <span className="material-symbols-outlined">close</span>
                  </span>
                </div>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default Courses;
